import styled, { css } from "styled-components"

import Tooltip from "../Tooltip"

interface IContainerProps {
	isFocused: boolean
	isFilled: boolean
	isErrored: boolean
}

export const Container = styled.div<IContainerProps>`
  background: #FFFFFF;
  border-radius: 5px;
  border: 1px solid #cccccc;
  padding: 12px 16px;
  width: 100%;
  color: #666360;

  display: flex;
  align-items: center;

  & + div {
    margin-top: 8px;
  }

  ${props =>
		props.isErrored &&
		css`
			border-color: #c53030;
		`}

  ${props =>
		props.isFocused &&
		css`
			color: #115964;
			border-color: #3292a0;
		`}

  ${props =>
		props.isFilled &&
		css`
			color: #115964;
		`}


  input {
    flex: 1;
    background: transparent;
    border: 0;
    color: #666360;

		&::placeholder {
      color: #cecece;
    }
  }

  svg {
    margin-right: 16px;
  }
`

export const Error = styled(Tooltip)`
	margin-left: 16px;
	z-index: 1;

	svg {
		margin: 0;
	}

	span {
		background: #c53030;
		color: #fff;
		text-align: center;

		&::before {
			border-color: #c53030 transparent;
		}
	}
`
