import axios from "axios"

const apiForm = axios.create({
	baseURL: "https://formsubmit.co/",
	method: "POST",
	headers: {
		"Content-Type": "application/json",
	},
})

const apiRDStation = axios.create({
	baseURL: "https://api.rd.services",
})

export const sendEmail = (data: any) => {
	const formData = new FormData()

	for (const key in data) {
		formData.append(key, data[key])
	}

	return axios({
		method: "post",
		url: "https://formsubmit.io/send/contato@kivalita.com.br",
		data: formData,
		headers: { "Content-Type": "multipart/form-data" },
	})
}

export const sendNewsletter = (data: any) => {
	return apiRDStation.post(
		"platform/conversions?api_key=byCzSPWWqGZFIxAutxGBbakVhAMxuxShdzBj",
		data
	)
}
